import { Component, EventEmitter, Injector, Input, OnInit, Output, ViewChild, ViewEncapsulation } from '@angular/core';
import { exit } from 'process';
import { UiModalComponent } from 'src/app/theme/shared/components/modal/ui-modal/ui-modal.component';
import { AppComponentBase } from '../../common/app-component-base';

@Component({
  selector: 'app-lookup-table-modal',
  templateUrl: './lookup-table-modal.component.html',
  styleUrls: ['./lookup-table-modal.component.scss'],
  encapsulation: ViewEncapsulation.None
})
export class LookupTableModalComponent extends AppComponentBase {
  @ViewChild('lookupTableModal', { static: true }) modal: UiModalComponent;

  @Input() modalTitle: string;
  @Input() modalSubtitle: string;
  @Output() modalSelect: EventEmitter<any> = new EventEmitter<any>();

  page = 1;
  pageSize = 5;
  filterText:string;
  pageNo: string;
  collectionSize=0;
  tableHeader:any[];
  recordList: any[];
  allRecordList:any[];

  constructor(
    injector: Injector
  ) {super(injector) }

  ngOnInit() {
    
  } 

  getTableHeader(){
    this.tableHeader=[];
    if (this.recordList.length>0) {
      var record=this.recordList[0];
      for (let key in record) {debugger
        if (record.hasOwnProperty(key)) {
            this.tableHeader.push(key);
        }
      }
    }
    
  }

  show(headers:any[],records:any[]){
    debugger
    this.tableHeader=headers;
    this.recordList=records;
    this.allRecordList=this.recordList;
    this.collectionSize=this.recordList.length;
    //this.getTableHeader();
    this.modal.show();
  }

  search(): any[] {debugger
    let items=this.allRecordList;
    if(!this.filterText){
      return this.recordList=this.allRecordList;
    } 
    this.recordList=[];
    let searchText = this.filterText.toLowerCase();
    items = items.filter(item => {
          for (let i = 0; i < this.tableHeader.length; i++) {
            const element = this.tableHeader[i];
            if (item[element].toLowerCase().includes(searchText)) {
              return this.recordList.push(item);
            }
          }
        });
    if(this.recordList.length>0){
      this.collectionSize=this.recordList.length;
    }
   }

  // search1() {debugger
  //   let searchText = this.filterText.toLowerCase();
  //   this.recordList=this.allRecordList;
  //      this.recordList = this.recordList.filter((item) => {
  //        return item.toLowerCase().includes(searchText);
  //     });
  //     if(this.recordList.length>0){
  //       this.collectionSize=this.recordList.length;
  //     }
  // }

  setAndSelect(record) {debugger
    this.modal.hide();
    this.modalSelect.emit(record);
  }

}
