import { Component, OnInit, ViewChild } from '@angular/core';
import {NgbTypeahead} from '@ng-bootstrap/ng-bootstrap';
import {Observable, Subject, merge, OperatorFunction} from 'rxjs';
import {debounceTime, distinctUntilChanged, filter, map} from 'rxjs/operators';
import { IPhone } from '../../interface/identity/phone-interface';
const items=['Home', 'Office', 'Mobile', 'Fax', 'Email', 'Skype', 'WhatsApp', 'Twitter', 'Extension', 'Web-Site', 'Other'];
@Component({
  selector: 'app-contact-detail',
  templateUrl: './contact-detail.component.html',
  styleUrls: ['./contact-detail.component.scss']
})
export class ContactDetailComponent implements OnInit {
  focus$ = [];
  click$ = [];

  contacts:IPhone[]=[];

  constructor() { }

  searchType(index,instance) {
    this.focus$.push(new Subject<string>());
    this.click$.push(new Subject<string>());
    return (text$: Observable<string>) => {
      const debouncedText$ = text$.pipe(
        debounceTime(200),
        distinctUntilChanged()
      );
      const clicksWithClosedPopup$ = this.click$[index].pipe(
        filter(() => !instance.isPopupOpen())
      );
      const inputFocus$ = this.focus$[index];

      return merge(debouncedText$, inputFocus$, clicksWithClosedPopup$).pipe(
        map(term =>
          (term === ""
            ? items
            : items.filter(
                v => v.toLowerCase().indexOf(term.toLowerCase()) > -1
              )
          ).slice(0, 10)
        )
      );
    };
  }

  ngOnInit() {
    this.insertContactitem();
  }
  insertContactitem(){
    var contact={ } as IPhone;
    this.contacts.push(contact);
  }
  removeContactitem(index){
    this.contacts.splice(index,1);
  }
}
